import React, {Component} from 'react';
import MaterialPanel from './MaterialPanel';
import TextField from 'material-ui/TextField';
import {GridList, GridTile} from 'material-ui/GridList';
import Checkbox from "material-ui/Checkbox"
import RaisedButton from 'material-ui/RaisedButton';

const styles = {
    gridList: {
        width: 500,
        height: 450,
        overflowY: 'auto',
    },
    autoComplete: {
        marginLeft: 30,
        marginTop: 14,
    },
    autoCompleteText: {
        fontSize: 'x-large',
    }
};


class TeamSelection extends Component {

    constructor(props, context) {
        super(props, context);
        this.props.setTitle("Login");
    }

    render() {
        return (
            <div>
                <div style={{
                    width: '30%', float: 'center',
                    margin: 'auto',
                    textAlign:"center",
                    fontFamily:'Roboto',
                    fontSize:"85px",
                    transform: 'translateY(40%)',
                    display: 'block',
                }}>
                    <strong>Team</strong>Up
                </div>
                <div style={{
                    width: '30%', float: 'center',
                    margin: 'auto',
                    top: '10%',
                    transform: 'translateY(5%)',
                    display: 'block',
                }}>
                    <MaterialPanel title="" titleColor="#ffffff" headerColor="#FFFFFF" height="55vh" body={
                        <GridList cellHeight='65' cols={1} rows={4}>
                            <GridTile style={{
                                paddingTop: 0,
                                paddingBottom: 0,
                                paddingLeft: 30,
                                paddingRight: 50,
                                fontSize: '27px',
                                textAlign: "center"
                            }}>
                                <strong>LOGIN</strong>
                            </GridTile>
                            <GridTile style={{
                                paddingTop: 0,
                                paddingBottom: 20,
                                paddingLeft: 30,
                                paddingRight: 50,
                            }}>
                                <TextField
                                    floatingLabelText="Email"
                                /><br/>
                                <br/>
                            </GridTile>
                            <GridTile style={{
                                paddingTop: 0,
                                paddingBottom: 0,
                                paddingLeft: 30,
                                paddingRight: 50,
                            }}>
                                <TextField
                                    floatingLabelText="Password"
                                    type="password"
                                /><br/>
                                <br/>
                            </GridTile>
                            <GridTile style={{padding: 20, paddingTop: 40}}>
                                <Checkbox label={"Remember Me"}/>

                            </GridTile>
                        </GridList>
                    }/>
                    <RaisedButton label="Login" primary={true} labelStyle={{fontSize:"18px"}} buttonStyle={{borderRadius:'50px',height:"50px"}} style={{
                        width:"70%",
                        float: 'center',
                        borderRadius:'50px',
                        margin: '12',
                        top: '50%',
                        transform: 'translate(21%,-100%)',
                    }} />
                </div>

            </div>
        );
    }
}

export default TeamSelection;



